import React from 'react';
import { Code, Smartphone, Globe, Database } from 'lucide-react';

const About = () => {
  const highlights = [
    {
      icon: <Code className="w-6 h-6" />,
      title: "Desarrollo Web",
      description: "Experiencia en React, Vue.js, Node.js y frameworks modernos"
    },
    {
      icon: <Smartphone className="w-6 h-6" />,
      title: "Desarrollo Mvil",
      description: "Aplicaciones nativas y hbridas con React Native y Flutter"
    },
    {
      icon: <Database className="w-6 h-6" />,
      title: "Backend & APIs",
      description: "Arquitectura de servidores robustos y APIs RESTful"
    },
    {
      icon: <Globe className="w-6 h-6" />,
      title: "DevOps & Cloud",
      description: "Despliegue y gestin en AWS, Docker y herramientas CI/CD"
    }
  ];

  return (
    <section id="about" className="py-20 px-4">
      <div className="container mx-auto max-w-6xl">
        <div className="text-center mb-16">
          <h2 className="text-3xl md:text-4xl font-bold mb-4  from-primary to-secondary bg-clip-text text-transparent">Acerca de M</h2>
          <p className="text-xl text-muted-foreground max-w-3xl mx-auto">
            Soy un desarrollador full-stack apasionado por crear soluciones innovadoras y eficientes. 
            Con varios aos de experiencia en desarrollo web y mvil, me especializo en tecnologas 
            modernas y siempre estoy buscando nuevos desafos.
          </p>
        </div>

        <div className="grid md:grid-cols-2 gap-12 items-center">
          {/* Content */}
          <div className="space-y-6">
            <div className="prose prose-lg max-w-none">
              <p className="text-muted-foreground leading-relaxed">
                Mi trayectoria como desarrollador comenz con la curiosidad por entender cmo 
                funcionan las cosas. Esta pasin me ha llevado a explorar diferentes tecnologas 
                y a mantenerme actualizado con las ltimas tendencias del desarrollo.
              </p>
              <p className="text-muted-foreground leading-relaxed">
                Me enfoco en escribir cdigo limpio, escalable y mantenible. Creo firmemente 
                en la importancia de la colaboracin en equipo y en la comunicacin efectiva 
                para lograr resultados excepcionales.
              </p>
              <p className="text-muted-foreground leading-relaxed">
                Cuando no estoy programando, me gusta contribuir a proyectos open source, 
                aprender nuevas tecnologas y compartir conocimientos con la comunidad de desarrolladores.
              </p>
            </div>

            {/* Stats */}
            <div className="grid grid-cols-2 gap-4 pt-6">
              <div className="text-center p-4 bg-gradient-to-br from-primary/5 to-secondary/5 rounded-lg border border-primary/10">
                <div className="text-2xl font-bold bg-emerald-700 from-primary to-secondary bg-clip-text text-transparent">50+</div>
                <div className="text-sm text-muted-foreground">Proyectos Completados</div>
              </div>
              <div className="text-center p-4 bg-gradient-to-br from-secondary/5 to-primary/5 rounded-lg border border-secondary/10">
                <div className="text-2xl font-bold bg-emerald-700 from-secondary to-primary bg-clip-text text-transparent">5+</div>
                <div className="text-sm text-muted-foreground">Aos de Experiencia</div>
              </div>
            </div>
          </div>

          {/* Highlights */}
          <div className="grid grid-cols-1 gap-6">
            {highlights.map((highlight, index) => (
              <div
                key={index}
                className="flex items-start space-x-4 p-6 bg-card rounded-lg border hover:shadow-lg transition-shadow"
              >
                <div className="p-3 bg-gradient-to-br from-primary/10 to-secondary/10 rounded-lg text-primary border border-primary/20">
                  {highlight.icon}
                </div>
                <div>
                  <h3 className="font-semibold mb-2">{highlight.title}</h3>
                  <p className="text-sm text-muted-foreground">{highlight.description}</p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default About;